export default {
  props: {
    songs: {
      type: Array,
      default: []
    }
  },
  methods: {
    _getDesc(song) {
      return `${song.singer} - ${song.album}`
    },
    click(item, index) {
      this.$emit('select', item, index)
    }
  }
}



