
import { mapGetters, mapMutations } from 'vuex'
import progressBar from 'base/progress-bar'
import XProgress from 'vux/src/components/x-progress/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'

import { playMode } from 'store/player/config'
import { randomList } from 'common/js/utils'
import Lyric from 'lyric-parser'

export default {
data() {
return {
ready: false,
currentTime: 0,
currentLyric: null,
playingLyric: ''
}
},
computed: {
mode() {
return this.getMode === playMode.sequence ? 'icon-sequence' : this.getMode === playMode.random ? 'icon-random' : 'icon-loop'
},
percent() {
return this.currentTime / this.getCurrentSong.duration * 100
},
cdCls() {
return this.getPlaying ? 'play' : 'play pause'
},
iconPlay() {
return this.getPlaying ? 'icon-pause' : 'icon-play'
},
iconPlayMini() {
return this.getPlaying ? 'icon-pause-mini' : 'icon-play-mini'
},
iconDis() {
return this.ready ? '' : 'disable'
},
...mapGetters([
'getPlayList',
'getFullScreen',
'getCurrentSong',
'getPlaying',
'getCurrentIndex',
'getMode',
'getSequenceList'
])
},
methods: {
handleLyric({lineNum, txt}) {
this.playingLyric = txt
},
getLyric() {
this.getCurrentSong.getLyric().then((lyric) => {
this.currentLyric = new Lyric(lyric, this.handleLyric)
if (this.getPlaying) {
this.currentLyric.play()
}
}).catch(() => {
this.currentLyric = null
this.playingLyric = ''
})
},
end() {
if (this.getMode === playMode.loop) {
this.$refs.audio.currentTime = 0
this.$refs.audio.play()
} else {
this.next()
}
},
setPlayMode() {
const mode = (this.getMode + 1) % 3
this.setMode(mode)
let list = null
if (mode === playMode.random) {
list = randomList(this.getSequenceList)
} else {
list = this.getSequenceList
}
this.resetCurrentIndex(list)
this.setPlayList(list)
},
resetCurrentIndex(list) {
let index = list.findIndex((item) => {
return item.id === this.getCurrentSong.id
})
this.setCurrentIndex(index)
},
formatTime(interval) {
interval = interval | 0
const min = interval / 60 | 0
let secFormat = interval % 60
let len = secFormat.toString().length
while (len < 2) {
secFormat = '0' + secFormat
len++
}
const sec = secFormat
return `${min}:${sec}`
},
updateTime(e) {
this.currentTime = e.target.currentTime
},
prev() {
if (!this.ready) {
return
}
let index = this.getCurrentIndex - 1
if (index === -1) {
index = this.getPlayList.length - 1
}
this.setCurrentIndex(index)
if (!this.getPlaying) {
this.togglePlay()
}
this.ready = false
},
next() {
if (!this.ready) {
return
}
let index = this.getCurrentIndex + 1
if (index === this.getPlayList.length) {
index = 0
}
this.setCurrentIndex(index)
if (!this.getPlaying) {
this.togglePlay()
}
this.ready = false
},
canPlay() {
this.ready = true
},
error() {
this.ready = true
},
togglePlay() {
if (!this.ready) {
return
}
this.setPlaying(!this.getPlaying)
if (this.currentLyric) {
this.currentLyric.togglePlay()
}
},
enter() {
},
afterEnter() {
},
leave() {
},
afterLeave() {
},
mini() {
this.setFullScreen(false)
},
max() {
this.setFullScreen(true)
},
...mapMutations({
setFullScreen: 'SET_FULLSCREEN',
setPlaying: 'SET_PLAYING',
setCurrentIndex: 'SET_CURRENTINDEX',
setMode: 'SET_MODE',
setPlayList: 'SET_PLAYLIST'
})
},
watch: {
getCurrentSong(newSong, oldSong) {
if (newSong.id === oldSong.id) {
return
}
if (this.currentLyric) {
this.currentLyric.stop()
this.playingLyric = ''
}
this.$nextTick(() => {
this.$refs.audio.play()
this.$refs.audio.volume = 0.2
this.getLyric()
})
},
getPlaying(newVal) {
this.$nextTick(() => {
const audio = this.$refs.audio
newVal ? audio.play() : audio.pause()
})
}
},
components: {
progressBar,
XProgress,
XCircle
}
}



