
import Blur from 'vux/src/components/blur/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import SongList from 'components/song'
import {mapActions, mapGetters} from 'vuex'

export default {
props: {
songs: {
type: Array,
default: []
},
title: {
type: String,
default: ''
},
avatar: {
type: String,
default: ''
}
},
watch: {
avatar() {
this.$nextTick(() => {
this.$refs.scrollerEvent.reset({top: 0})
})
},
songs() {
this.$nextTick(() => {
this.$refs.scrollerEvent.reset({top: 0})
})
}
},
data() {
return {
}
},
created() {
},
computed: {
...mapGetters(['getPlayList'])
},
methods: {
random() {
let fullScreen = !(this.getPlayList.length > 0)
this.randomPlayer({
playlist: this.songs,
fullScreen: fullScreen
})
},
_getDesc(song) {
return `${song.singer} - ${song.album}`
},
onScroll (pos) {
},
back() {
this.$router.back()
},
select(item, index) {
let fullScreen = !(this.getPlayList.length > 0)
this.initPlayer({
playlist: this.songs,
index: index,
fullScreen: fullScreen
})
},
...mapActions([
'initPlayer',
'randomPlayer'
])
},
components: {
Blur,
Scroller,
XHeader,
SongList
}
}



